import React, { Component } from 'react';
import HeaderBar from './App';
import LoadBuffer from './LoadBuffer';
import Registers from './Registers';
import IssueTable from './IssueTable';
import ReservationStation from './ReservationStation';
import Settings from './Settings';
import { Layout } from 'antd';
import { Modal } from 'antd';
import { message } from 'antd';
import { Input, notification } from 'antd';
import { Row, Col } from 'antd';

const { Header, Content, Footer } = Layout;
const { TextArea } = Input;


class UI extends Component {

    constructor(props) {
        const openNotification = (title, description) => {
            message.success(description);
        };
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
        this.handleOk = this.handleOk.bind(this);
        const registers_columns = [...Array(32).keys()].map(i => {
            return ({
                title: i,
                dataIndex: i,
                key: i
            }
            )
        });
        var websocket = new WebSocket("ws://127.0.0.1:5678/");
        var context = this;
        websocket.onmessage = function (event) {
            if (event.data.slice(0, 3) === "005") {
                // Server has successfully received the NEL file
                openNotification("成功", "服务器成功收到程序并运行。");

                context.setState({RS_ALL:new Array()});
                context.setState({LoadBuffer_ALL: new Array()});
                context.setState({R_ALL: new Array()});

                // Processing data
                const data_json = event.data.slice(3);
                const data_parse = JSON.parse(data_json);
                // console.log(data_parse)
                data_parse.forEach(function (item, index) {

                    let RS_single = new Array();
                    let LB_single = new Array();
                    let R_single = new Array();
                    // console.log(item);
                    item.forEach(function (item0, index0) {
                        // console.log(item0.name.charAt(0))
                        if (item0.name.charAt(0) === 'A') {
                            // Add RS
                            RS_single.push(item0);
                        } else if (item0.name.charAt(0) === 'M') {
                            // Mul RS
                            RS_single.push(item0);

                        } else if (item0.name.charAt(0) === 'L') {
                            // LB
                            LB_single.push(item0);
                        } else {
                            // R
                            R_single.push(item0);
                        }
                    });;
                    const myRS_single = RS_single.slice()
                    context.state.RS_ALL.push(myRS_single);
                    const myLB_single = LB_single.slice();
                    context.state.LoadBuffer_ALL.push(myLB_single);
                    const myR_single = R_single.slice();
                    context.state.R_ALL.push(myR_single);
                });

                context.setState({
                    current_cycle: 1,
                    all_cycle: data_parse.length,
                });
            } else if (event.data.slice(0, 3) === "006") {
                const data_json = event.data.slice(3);
                const data_parse = JSON.parse(data_json);
                context.setState({
                    IssueTable: data_parse,
                })
            } else if (event.data.slice(0, 3) === "000") {
                // Settings
                const data_json = event.data.slice(3);
                const data_parse = JSON.parse(data_json);
                context.setState({
                    RS_ADD: data_parse.RS_ADD,
                    RS_MUL: data_parse.RS_MUL,
                    R: data_parse.R,
                    LD: data_parse.LD,
                    RS_ADD_ALU: data_parse.RS_ADD_ALU,
                    RS_MUL_ALU: data_parse.RS_MUL_ALU,
                    LD_ALU: data_parse.LD_ALU,
                    ADD: data_parse.ADD,
                    SUB: data_parse.SUB,
                    MUL: data_parse.MUL,
                    DIV: data_parse.DIV,
                    JUMP: data_parse.JUMP,
                    LOAD: data_parse.LOAD,
                })
            } else if (event.data.slice(0, 3) === "008") {
                openNotification("设置成功", "服务器成功收到设置。");
            } else if (event.data.slice(0, 3) === "020") {
                openNotification("连接成功", "成功连接到服务器。");
            }
        };

        this.state = {
            showSettings: false,
            RS_ADD: null,
            RS_MUL: null,
            R: null,
            LD: null,
            RS_ADD_ALU: null,
            RS_MUL_ALU: null,
            LD_ALU: null,
            ADD: 0,
            SUB: 0,
            MUL: 0,
            DIV: 0,
            JUMP: 0, 
            LOAD: 0, 
            jumpTo: 1,
            showIssueTable: true,
            current_cycle: 0,
            all_cycle: 0,
            visible: false,
            ws: websocket,
            nel: '',
            LoadBuffer_ALL: new Array(),
            RS_ALL: new Array(),
            R_ALL: new Array(),
            IssueTable: null,
            LoadBuffer: {
                dataSource: null,
                columns: [
                    {
                        title: 'LoadBuffer',
                        dataIndex: 'name',
                        key: 'name',
                    },
                    {
                        title: 'Busy',
                        dataIndex: 'busy',
                        key: 'busy',
                    },
                    {
                        title: 'Address',
                        dataIndex: 'address',
                        key: 'address',
                    },
                    {
                        title: '状态',
                        dataIndex: 'state',
                        key: 'state',
                    },
                    {
                        title: '剩余EXEC周期',
                        dataIndex: 'remainingcycle',
                        key: 'remainingcycle',
                    }
                ],
            },

            Registers: {
                dataSource: null,
                columns: registers_columns,
            },

            IssueTables: {
                dataSource: null,
                columns: [
                    {
                        title: '指令',
                        dataIndex: 'instruction',
                        key: 'instruction',
                    },
                    {
                        title: 'Issue',
                        dataIndex: 'issue',
                        key: 'issue',
                    },
                    {
                        title: 'ExecComp',
                        dataIndex: 'execcomp',
                        key: 'execcomp',
                    },
                    {
                        title: 'Write',
                        dataIndex: 'writeback',
                        key: 'writeback',
                    },
                ]
            },

            ReservationStation: {
                dataSource: null,
                columns: [
                    {
                        title: '保留站',
                        dataIndex: 'name',
                        key: 'name',
                    },
                    {
                        title: 'Busy',
                        dataIndex: 'busy',
                        key: 'busy',
                    },
                    {
                        title: 'Op',
                        dataIndex: 'op',
                        key: 'op',
                    },
                    {
                        title: 'Vj',
                        dataIndex: 'vj',
                        key: 'vj',
                    },
                    {
                        title: 'Vk',
                        dataIndex: 'vk',
                        key: 'vk',
                    },
                    {
                        title: 'Qj',
                        dataIndex: 'qj',
                        key: 'qj',
                    },
                    {
                        title: 'Qk',
                        dataIndex: 'qk',
                        key: 'qk',
                    },
                    {
                        title: '状态',
                        dataIndex: 'state',
                        key: 'state',
                    },
                    {
                        title: '剩余EXEC周期',
                        dataIndex: 'remainingcycle',
                        key: 'remainingcycle',
                    },
                ]
            }
        }
    };

    sendNel () {
        this.setState({
            visible: true,
        })
    };

    handleChange (event) {
        this.setState({
            nel: event.target.value,
        })
    };

    handleOk () {
        this.state.ws.send("001" + this.state.nel)
        this.setState({
            visible: false
        })
    };

    handleCancel () {
        this.setState({
            visible: false
        })
    };

    handleNext () {
        const now_cycle = this.state.current_cycle + 1;
        if (now_cycle > this.state.all_cycle) {
            return;
        }
        this.setState({
            current_cycle: now_cycle,
        })
    };

    handleBefore () {
        const now_cycle = this.state.current_cycle - 1;
        if (now_cycle < 1) {
            return;
        }
        this.setState({
            current_cycle: now_cycle,
        })
    };

    changeIssueTable () {
        const show = !this.state.showIssueTable;
        this.setState({
            showIssueTable: show,
        })
    };

    jumpTo (page) {
        console.log(this.state.jumpTo)
        if (page < 1 || page > this.state.all_cycle) {
            return;
        }
        this.setState({
            jumpTo: page,
        })
    };

    handleJump () {
        this.setState({
            current_cycle: this.state.jumpTo,
        })
    };

    changeSettings () {
        const show = !this.state.showSettings;
        this.setState({
            showSettings: show,
        })
    };

    onChangeRSADD (value) {
        // Send to the server
        this.state.ws.send("007" + value)
        this.setState({
            RS_ADD: value,
        })
    };

    onChangeRSMUL (value) {
        this.state.ws.send("008" + value)
        this.setState({
            RS_MUL: value,
        })
    };

    onChangeLB (value) {
        this.state.ws.send("009" + value)
        this.setState({
            LD: value,
        })
    };

    onChangeRSADDALU (value) {
        this.state.ws.send("010" + value)
        this.setState({
            RS_ADD_ALU: value,
        })
    };

    onChangeRSMULALU (value) {
        this.state.ws.send("011" + value)
        this.setState({
            RS_MUL_ALU: value,
        })
    };

    onChangeLBALU (value) {
        this.state.ws.send("012" + value)
        this.setState({
            LD_ALU: value,
        })
    };

    onChangeADD (value) {
        this.state.ws.send("013" + value)
        this.setState({
            ADD: value,
        })
    };

    onChangeSUB (value) {
        this.state.ws.send("014" + value)
        this.setState({
            SUB: value,
        })
    };

    onChangeMUL (value) {
        this.state.ws.send("015" + value)
        this.setState({
            MUL: value,
        })
    };

    onChangeDIV (value) {
        this.state.ws.send("016" + value)
        this.setState({
            DIV: value,
        })
    };

    onChangeJUMP (value) {
        this.state.ws.send("017" + value)
        this.setState({
            JUMP: value,
        })
    };

    onChangeLOAD (value) {
        this.state.ws.send("018" + value)
        this.setState({
            LOAD: value,
        })
    };

    onClickTest () {
        this.state.ws.send("019")
    };

    render () {
        return (
            <div>
                <Modal
                    title="上传NEL程序:⚠️请保证程序严格符合语法"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <TextArea rows={4} onChange={this.handleChange} />
                </Modal>

                <Layout style={{ background: 'rgba(255, 255, 255, 0.2)' }}>
                    <Header style={{ background: 'rgba(204, 255, 229, 1)', height: '30px' }}></Header>
                    <Header style={{ background: 'rgba(204, 255, 229, 1)', height: '70px' }} >
                        <HeaderBar
                            onClick={() => this.sendNel()}
                            currentCycle={this.state.current_cycle}
                            changeIssueTable={() => this.changeIssueTable()}
                            handleNext={() => this.handleNext()}
                            handleBefore={() => this.handleBefore()}
                            allCycle={this.state.all_cycle}
                            jumpTo={(value) => this.jumpTo(value)}
                            handleJump={(value) => this.handleJump(value)}
                            changeSettings={()=>this.changeSettings()}
                            onClickTest={()=>this.onClickTest()}
                        />
                    </Header>

                    <Row>
                        <Col>
                            {this.state.showSettings ? <Content style={{ padding: '0 90px', margin: '30px 24px 16px 0' }}>
                            <Settings 
                                onChangeRSADD={(value)=>this.onChangeRSADD(value)}
                                onChangeRSMUL={(value)=>this.onChangeRSMUL(value)}
                                onChangeLB={(value)=>this.onChangeLB(value)}
                                onChangeRSADDALU={(value)=>this.onChangeRSADDALU(value)}
                                onChangeRSMULALU={(value)=>this.onChangeRSMULALU(value)}
                                onChangeLBALU={(value)=>this.onChangeLBALU(value)}
                                RS_ADD={this.state.RS_ADD}
                                RS_ADD_ALU={this.state.RS_ADD_ALU}
                                RS_MUL={this.state.RS_MUL}
                                RS_MUL_ALU={this.state.RS_MUL_ALU}
                                LD={this.state.LD}
                                LD_ALU={this.state.LD_ALU}
                                ADD={this.state.ADD}
                                SUB={this.state.SUB}
                                MUL={this.state.MUL}
                                DIV={this.state.DIV}
                                JUMP={this.state.JUMP}
                                LOAD={this.state.LOAD}
                                onChangeADD={(value)=>this.onChangeADD(value)}
                                onChangeSUB={(value)=>this.onChangeSUB(value)}
                                onChangeMUL={(value)=>this.onChangeMUL(value)}
                                onChangeDIV={(value)=>this.onChangeDIV(value)}
                                onChangeJUMP={(value)=>this.onChangeJUMP(value)}
                                onChangeLOAD={(value)=>this.onChangeLOAD(value)}
                            />
                            </Content> : null}
                        </Col>
                        <Col>
                            {this.state.showIssueTable ? <Content style={{ padding: '0 90px', margin: '30px 24px 16px 0' }}>
                                <IssueTable dataSource={this.state.IssueTable} columns={this.state.IssueTables.columns}></IssueTable>
                            </Content> : null}
                        </Col>

                        <Col>
                            <Content style={{ padding: '0 90px', margin: '30px 24px 16px 0' }}>
                                <ReservationStation dataSource={this.state.RS_ALL[this.state.current_cycle - 1]} columns={this.state.ReservationStation.columns} />
                            </Content>          
                            <Content style={{ padding: '0 90px', margin: '16px 24px 16px 0' }}>
                                <LoadBuffer dataSource={this.state.LoadBuffer_ALL[this.state.current_cycle - 1]} columns={this.state.LoadBuffer.columns} />
                            </Content>
                            <Content style={{ padding: '0 90px', margin: '16px 24px 16px 0' }}>
                                <Registers dataSource={this.state.R_ALL[this.state.current_cycle - 1]} columns={this.state.Registers.columns} />
                            </Content>
                        </Col>
                    </Row>

                    <Footer style={{ textAlign: 'center', background: 'rgba(255, 255, 255, 0.2)' }}>
                        梁聪@THU liangxcong@gmail.com
                    </Footer>
                </Layout>
            </div>
        );
    }
}

export default UI;
