import React, { Component } from 'react';
import './App.css';
import Table from 'antd/lib/table'

class LoadBuffer extends Component {

    render () {

        return (
            <div>
                <Table
                    dataSource={this.props.dataSource}
                    columns={this.props.columns}
                    bordered
                    pagination = {false}
                />
            </div>
        );
    }
}

export default LoadBuffer;