import React, { Component } from 'react';
import './App.css';
import { Typography } from 'antd';
import { Row, Col } from 'antd';
import { Button } from 'antd';
import { Switch } from 'antd';
import { InputNumber } from 'antd';

const { Title } = Typography;

class HeaderBar extends Component {
  constructor (props) {
    super(props);
    this.onChange = this.onChange.bind(this);
  };

  onChange (value) {
    this.props.jumpTo(value);
  };

  render () {
    return (
      <Row style={{ overflow: 'hidden', whiteSpace: 'nowrap'}}>
        <Col span={10}><Title>TOMASULO算法模拟器(周期{this.props.currentCycle}/{this.props.allCycle})</Title></Col>
        <Col span={4}>
          设置
          <Switch checkedChildren="开" unCheckedChildren="关" defaultChecked={false} onChange={() => this.props.changeSettings()} />
          发射表
          <Switch checkedChildren="开" unCheckedChildren="关" defaultChecked={true} onChange={() => this.props.changeIssueTable()} />
        </Col>
        <Col span={7}>
          <Button onClick={() => this.props.handleBefore()}>后退</Button>
          <Button onClick={() => this.props.handleNext()} style={{marginLeft:'30px'}}>前进</Button>
          <InputNumber min={1} max={this.props.allCycle} defaultValue={this.props.currentCycle } style={{marginLeft:'50px'}} onChange={this.onChange} />
          <Button onClick={() => this.props.handleJump()} style={{marginLeft:'30px'}}>跳转</Button>
        </Col>
        <Col span={3} style={{paddingRight:'0px'}}>
          <Button onClick={this.props.onClick} type="primary">传入NEL程序</Button>
          <Button onClick={this.props.onClickTest} style={{marginLeft:'5px'}}>测试连接</Button>
        </Col>
      </Row>
    );
  }
}

export default HeaderBar;